	import avatar from "../yq-avatar.vue";
	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	export default {
		data() {
			return {
				array: ["供应信息","需求信息"],
				array1:[0,1],
                index: "",
				selectValue:"", 
				imageList:[],
				title:"",
				textarea:"",
				pathArr:[],
				listPath:[],
				phone:"",
				percent:0,
				showBox:false,
				state:false,
				urls: ["../../static/img/timg1.jpg","../../static/logo.png"],
				thumbnail:'../../static/img/timg1.jpg'
			}
		},
		onLoad:function(){
			var _self = this 
		},
		onShow:function(){
			var _self  = this
			let pages = getCurrentPages();
			let currPage = pages[pages.length-1];
			if(currPage.data.imageList==undefined || currPage.data.imageList=='' || currPage.data == undefined){
				
			}else{
				_self.imageList = []
				_self.pathArr = []
				_self.listPath = []
				console.log(_self.pathArr)
			}
			
		}, 
		methods: {
			/**
			 * 删除数组图片
			 */
			deletePic:function(e){
				var _self = this
				console.log(e)
				_self.imageList.splice(e,1)
				_self.pathArr.splice(e,1)
			},
			/**
			 * @param {Object} e缩略图
			 */
			doBefore() {
				console.log('doBefore');
			},
			clk(index) {
				this.$refs.avatar.fChooseImg(index,{
					selWidth: '350upx', selHeight: '350upx', 
					expWidth: '260upx', expHeight: '260upx', 
					inner: index ? 'true' : 'false' 
				});
			},
			doUpload(rsp) {
				var _self = this
				console.log(rsp);
				uni.uploadFile({
					url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
					//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
					filePath: rsp.path,
					name: 'file', 
					formData: {
						'user': 'test'
					},
					success: (uploadFileRes) => {
						console.log(uploadFileRes)
						_self.thumbnail = uploadFileRes.data.replace("\"","").replace("\"","")
					}
				  }); 
			},
			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.index = e.target.value 
			},
			previewImage: function(e) {
				// var current = e.target.dataset.src
				// uni.previewImage({
				// 	current: current,
				// 	urls: this.imageList
				// })
			},
			chooseImage: async function() {
				var _self = this
					if (this.imageList.length === 9) {
						let isContinue = await this.isFullImg();
						console.log("是否继续?", isContinue);
						if (!isContinue) {
							return;
						}
					}
					uni.chooseImage({
						success: (res) => {
							console.log(res)
							console.log(JSON.stringify(res.tempFilePaths));
							var tempFilePaths = res.tempFilePaths;
							this.imageList = this.imageList.concat(res.tempFilePaths);
							for(var i =0;i<tempFilePaths.length;i++){
							uni.uploadFile({
								url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
								//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
								filePath: tempFilePaths[i],
								name: 'file', 
								formData: {
									'user': 'test'
								},
								success: (uploadFileRes) => {
									_self.pathArr = _self.pathArr.concat(JSON.parse(uploadFileRes.data))
									console.log(_self.pathArr)
								}
							  }); 
							}
							_self.state = true 
						} 
					})
				},
				isFullImg: function() {
					return new Promise((res) => {
						uni.showModal({
							content: "已经有9张图片了,是否清空现有图片？",
							success: (e) => {
								if (e.confirm) {
									this.imageList = [];
									res(true);
								} else {
									res(false)
								}
							},
							fail: () => {
								res(false)
							}
						})
					})
				},
			showOrHide:function(){
					this.showBox = false
				},
			addInfo:function(){
				
				var _self = this
				console.log(_self.pathArr)
				var myreg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
				if(_self.index == ""){
					uni.showToast({
						title:"请选择供求",
						icon:"none"
					})
					return false
				}else if(_self.title == ""){
					uni.showToast({
						title:"请输入标题",
						icon:"none"
					})
					return false
				}
				// else if(_self.percent != 100 && _self.imageList.length>0){
				// 
				// 	uni.showToast({
				// 		title:"图片正在上传，请稍后提交",
				// 		icon:"none"
				// 	})
				// 	return false
				// }
				else if(_self.state == false && _self.imageList.length>0){ 
					uni.showToast({
						title:"图片正在上传，请稍后提交",
						icon:"none"  
					})
					return false
				}
				else if(_self.textarea == ""){
					uni.showToast({
						title:"请输入简介",
						icon:"none"
					})
					return false
				}
				else if(_self.thumbnail == ''){
					uni.showToast({
						title:"请上传缩略图",
						icon:"none"
					})
					return false
				}
				else if(_self.phone == ""){
					uni.showToast({
						title:"请输入联系方式",
						icon:"none"
					})
					return false
				}else if(!myreg.test(_self.phone)){
						uni.showToast({
						title:"联系电话格式不正确",
						icon:"none"
					})
					return false
				}
				for(var i =0;i<_self.pathArr.length;i++){
						_self.listPath.push({testName1:_self.pathArr[i]})
						console.log(_self.listPath)
					}
				_self.listPath.push({phone:_self.phone,mark:_self.index,name:_self.title,sketch:_self.textarea,optionId:uni.getStorageSync("openId"),smallPrimary:_self.thumbnail})
					console.log(JSON.stringify(_self.listPath))
				uni.request({
					url:_self.$api+"dockingManager/declareAdd",
					data:JSON.stringify(_self.listPath), 
					method:"POST",
					success:function(res){ 
						if(res.data ==98){
							uni.showToast({
								title:"您尚未认证",
								icon:'none'
							})
							return false
						}else if(res.data ==99){
							uni.showModal({
							title: '提示',
							content: '此操作需用户授权，是否进行授权',
							success: function (res) {
								if (res.confirm) {
									//跳转到授权页面  
									uni.navigateTo({
										url:"../login/login"  
									})
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
		
							return false
						}else if(res.data ===0){
								uni.showToast({
								title:"发布失败"
						})
							return false
						}else if(res.data ==1){
								uni.showToast({
								title:"发布成功",
								success:function(){
									setTimeout(function(){
										uni.switchTab({
											url:"../index/index"
										})
									},1000)
								}
							})
							return false
						}else{
							uni.showToast({
								title:"系统异常"
							})
							return false
						}
						console.log(res)
					} 
					
				})
			}
		},
		 components: {uniCollapse,uniCollapseItem,avatar}
	}
